export default class Api {

    constructor(serverAdress, authorizationToken) {
        this.server = serverAdress;
        this.token = authorizationToken;
    }

    getUserInfoFromServer() {
        return fetch(`${this.server}/users/me`, {
            headers: {
                authorization: this.token,
            },
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,        ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    patchUserInfo(newUserName, newUserAbout) {
        return fetch(`${this.server}/users/me`, {
            method: 'PATCH',
            headers: {
                authorization: this.token,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                'name': `${newUserName}`,
                'about': `${newUserAbout}`,
            }),
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,        ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    getPicturesFromServer() {
        return fetch(`${this.server}/cards`, {
            headers: {
                authorization: this.token
            },
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,      ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    postNewCard(name, link) {
        return fetch(`${this.server}/cards`, {
            method: 'POST',
            headers: {
                authorization: this.token,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                'name': `${name}`,
                'link': `${link}`,
            }),
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,    ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    deleteCardFromServer(_id) {
        return fetch(`${this.server}/cards/${_id}`, {
            method: 'DELETE',
            headers: {
                authorization: this.token,
            },
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,      ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    putLike(_id) {
        return fetch(`${this.server}/cards/like/${_id}`, {
            method: 'PUT',
            headers: {
                authorization: this.token,
            },
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,    ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    deleteLike(_id) {
        return fetch(`${this.server}/cards/like/${_id}`, {
            method: 'DELETE',
            headers: {
                authorization: this.token,
            },
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,    ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    getAvatar() {
        return fetch(`${this.server}/users/me/`, {
            headers: {
                authorization: this.token,
            },
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,      ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

    patchAvatar(newUrl) {
        return fetch(`${this.server}/users/me/avatar`, {
            method: 'PATCH',
            headers: {
                authorization: this.token,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                'avatar': `${newUrl}`,
            }),
        })
            .then(res => {
                if (res.ok) return res.json();
                return Promise.reject(`,      ( : ${res.status} ${res.statusText}`)
            })
            .catch((err) => {
                alert(err)
            });
    }

}

